import {
  VXFormExtension,
  VXFormConfig,
  TemplateManipulators
} from "../../services/vxform.config";
import {
  VXFormFieldConfigCache,
  VXFormFieldConfig
} from "../../components/vxform.field.config";
import { FormGroup } from "@angular/forms";
import {
  getFieldId,
  assignModelValue,
  isUndefined,
  getFieldValue,
  reverseDeepMerge,
  getKeyPath
} from "../../utils";
import { Guid } from "../../guid";

export class CoreExtension implements VXFormExtension {
  private formId = 0;
  constructor(private vxFormConfig: VXFormConfig) {}

  prePopulate(field: VXFormFieldConfigCache) {
    this.getFieldComponentInstance(field).prePopulate();
    if (field.parent) {
      return;
    }

    const fieldTransforms =
      (field.options && field.options.fieldTransform) ||
      this.vxFormConfig.extras.fieldTransform;
    (Array.isArray(fieldTransforms)
      ? fieldTransforms
      : [fieldTransforms]
    ).forEach(fieldTransform => {
      if (fieldTransform) {
        const fieldGroup = fieldTransform(
          field.fieldGroup,
          field.model,
          <FormGroup>field.formControl,
          field.options
        );
        if (!fieldGroup) {
        }
      }
    });
  }

  onPopulate(field: VXFormFieldConfigCache) {
    this.initFieldOptions(field);
    this.getFieldComponentInstance(field).onPopulate();
    if (field.fieldGroup) {
      field.fieldGroup.forEach((f, index) => {
        Object.defineProperty(f, "parent", {
          get: () => field,
          configurable: true
        });
        Object.defineProperty(f, "index", {
          get: () => index,
          configurable: true
        });
        this.formId++;
      });
    }
  }

  postPopulate(field: VXFormFieldConfigCache) {
    this.getFieldComponentInstance(field).postPopulate();
  }

  private initFieldOptions(field: VXFormFieldConfigCache) {
    const root = <VXFormFieldConfigCache>field.parent;
    if (!root) {
      return;
    }

    Object.defineProperty(field, "form", {
      get: () => root.formControl,
      configurable: true
    });
    Object.defineProperty(field, "options", {
      get: () => root.options,
      configurable: true
    });
    Object.defineProperty(field, "model", {
      get: () =>
        field.key && field.fieldGroup ? getFieldValue(field) : root.model,
      configurable: true
    });

    reverseDeepMerge(field, {
      id: getFieldId(`vxform_${this.formId}`, field, field["index"]),
      guid: Guid.create()["value"],
      hooks: {},
      modelOptions: {},
      templateOptions:
        !field.type || !field.key
          ? {}
          : {
              label: "",
              placeholder: "",
              focus: false,
              disabled: false
            }
    });

    if (field.lifecycle) {
    }

    if (field.template && field.type !== "template") {
      if (field.type) {
      }
      field.type = "template";
    }

    if (!field.type && field.fieldGroup) {
      field.type = "vxform-group";
    }

    if (field.type) {
      this.vxFormConfig.getMergedField(field);
    }

    if (!isUndefined(field.defaultValue) && isUndefined(getFieldValue(field))) {
      assignModelValue(root.model, getKeyPath(field), field.defaultValue);
    }

    this.initFieldWrappers(field);
  }

  private initFieldWrappers(field: VXFormFieldConfig) {
    field.wrappers = field.wrappers || [];
    const fieldTemplateManipulators: TemplateManipulators = {
      preWrapper: [],
      postWrapper: [],
      ...(field.templateOptions.templateManipulators || {})
    };

    field.wrappers = [
      ...this.vxFormConfig.templateManipulators.preWrapper.map(m => m(field)),
      ...fieldTemplateManipulators.preWrapper.map(m => m(field)),
      ...field.wrappers,
      ...this.vxFormConfig.templateManipulators.postWrapper.map(m => m(field)),
      ...fieldTemplateManipulators.postWrapper.map(m => m(field))
    ].filter((el, i, a) => el && i === a.indexOf(el));
  }

  private getFieldComponentInstance(field: VXFormFieldConfigCache) {
    const componentRef = this.vxFormConfig.createComponent(field);
    const instance: VXFormExtension = componentRef
      ? (componentRef.instance as any)
      : {};

    return {
      prePopulate: () => instance.prePopulate && instance.prePopulate(field),
      onPopulate: () => instance.onPopulate && instance.onPopulate(field),
      postPopulate: () => instance.postPopulate && instance.postPopulate(field)
    };
  }
}
