import { VXFormExtension } from "../../services/vxform.config";
import { VXFormFieldConfigCache } from "../../components/vxform.field.config";
import {
  AbstractControl,
  FormGroup,
  FormControl,
  AbstractControlOptions
} from "@angular/forms";
import { getKeyPath, getFieldValue, defineHiddenProp } from "../../utils";
import { registerControl } from "./utils";

export class FieldFormExtension implements VXFormExtension {
  onPopulate(field: VXFormFieldConfigCache) {
    if (field.key) {
      this.addFormControl(field);
    }

    if (field.parent && field.fieldGroup && !field.key) {
      defineHiddenProp(field, "formControl", field.parent.formControl);
    }
  }

  postPopulate(field: VXFormFieldConfigCache) {
    if (field.parent) {
      return;
    }

    const updateValidity = this.setValidators(field);
    updateValidity &&
      (field.formControl as any)._updateTreeValidity({ emitEvent: false });
  }

  private addFormControl(field: VXFormFieldConfigCache) {
    const controlOptions: AbstractControlOptions = {
      updateOn: field.modelOptions.updateOn
    };
    let control: AbstractControl;

    const form = field.parent.formControl as FormGroup;
    const value = getFieldValue(field);
    const paths = getKeyPath(field);
    if (
      field.formControl instanceof AbstractControl ||
      (form && form.get(paths))
    ) {
      control = field.formControl || form.get(paths);
    } else if (
      field._componentFactory &&
      field._componentFactory.component &&
      field._componentFactory.component.createControl
    ) {
      const component = field._componentFactory.component;

      control = component.createControl(value, field);
    } else if (field.fieldGroup) {
      control = new FormGroup({}, controlOptions);
    } else {
      control = new FormControl(value, controlOptions);
    }

    registerControl(field, control);
  }

  private setValidators(field: VXFormFieldConfigCache) {
    let updateValidity = false;
    if (field.key) {
      const {
        _validators: validators,
        _asyncValidators: asyncValidators,
        formControl: control,
        templateOptions: { disabled }
      } = field;

      if (disabled && control.enabled) {
        control.disable({ emitEvent: false, onlySelf: true });
        updateValidity = true;
      }

      if (validators !== control.validator) {
        control.setValidators(validators);
        updateValidity = true;
      }
      if (asyncValidators !== control.asyncValidator) {
        control.setAsyncValidators(asyncValidators);
        updateValidity = true;
      }
    }

    (field.fieldGroup || []).forEach(
      f => this.setValidators(f) && (updateValidity = true)
    );

    return updateValidity;
  }
}
