import { Inject, Optional } from "@angular/core";
import { FormArray } from "@angular/forms";
import { FieldType } from "./field.type";
import {
  clone,
  isNullOrUndefined,
  assignModelValue,
  getKeyPath
} from "../utils";
import { VXFormBuilder } from "../services/vxform.builder";
import { VXFormFieldConfig } from "../components/vxform.field.config";
import { VXFORM_CONFIG, VXFormExtension } from "../services/vxform.config";
import {
  registerControl,
  unregisterControl
} from "../extensions/field-form/utils";

export abstract class FieldArrayType<
  F extends VXFormFieldConfig = VXFormFieldConfig
> extends FieldType<F> implements VXFormExtension {
  formControl: FormArray;
  defaultOptions: any = {
    defaultValue: []
  };

  constructor(@Inject(VXFORM_CONFIG) @Optional() builder?: VXFormBuilder) {
    super();

    if (builder instanceof VXFormBuilder) {
    }
  }

  onPopulate(field: VXFormFieldConfig) {
    if (!field.formControl) {
      registerControl(
        field,
        new FormArray([], { updateOn: field.modelOptions.updateOn })
      );
    }

    field.fieldGroup = field.fieldGroup || [];

    const length = field.model ? field.model.length : 0;
    if (field.fieldGroup.length > length) {
      for (let i = field.fieldGroup.length - 1; i >= length; --i) {
        unregisterControl(field.fieldGroup[i]);
        field.fieldGroup.splice(i, 1);
      }
    }

    for (let i = field.fieldGroup.length; i < length; i++) {
      const f = { ...clone(field.fieldArray), key: `${i}` };
      field.fieldGroup.push(f);
    }
  }

  add(i?: number, initialModel?: any) {
    i = isNullOrUndefined(i) ? this.field.fieldGroup.length : i;
    if (!this.model) {
      assignModelValue(this.field.parent.model, getKeyPath(this.field), []);
    }

    this.model.splice(i, 0, initialModel ? clone(initialModel) : undefined);

    (<any>this.options)._buildForm(true);
    this.formControl.markAsDirty();
  }

  remove(i: number) {
    this.model.splice(i, 1);
    unregisterControl(this.field.fieldGroup[i]);
    this.field.fieldGroup.splice(i, 1);
    this.field.fieldGroup.forEach((f, key) => (f.key = `${key}`));

    (<any>this.options)._buildForm(true);
    this.formControl.markAsDirty();
  }
}
