import { Input } from "@angular/core";
import { FormGroup } from "@angular/forms";
import {
  VXFormFieldConfig,
  VXAttachmentFile
} from "../components/vxform.field.config";

export abstract class FieldType<
  F extends VXFormFieldConfig = VXFormFieldConfig
> {
  @Input() field: F;
  defaultOptions?: F;

  @Input()
  get model() {
    return this.field.model;
  }
  set model(m: any) {}

  @Input()
  get form() {
    return <FormGroup>this.field.parent.formControl;
  }
  set form(form) {}

  @Input()
  get options() {
    return this.field.options;
  }
  set options(options: F["options"]) {}

  get key() {
    return this.field.key;
  }

  get formControl() {
    return this.field.formControl;
  }

  get to() {
    return this.field.templateOptions || {};
  }

  get showError(): boolean {
    return this.options.showError(this);
  }

  get id(): string {
    return this.field.id;
  }

  get formState() {
    return this.options.formState || {};
  }

  get formStatus() {
    return this.field.status || 0;
  }

  get editor() {
    if (this.field.hasOwnProperty("editor")) {
      return this.field.editor;
    }
    return true;
  }

  get text() {
    if (this.model.hasOwnProperty(this.field.key)) {
      return this.model[this.field.key];
    }
    return "";
  }

  get placeHolder() {
    if (
      this.to != undefined &&
      this.to != null &&
      this.to.placeholder != undefined &&
      this.to.placeholder != null
    ) {
      return this.to.placeholder;
    }
    return "";
  }

  get textSingle() {
    let result = "";
    if (this.model.hasOwnProperty(this.field.key)) {
      let value = this.model[this.field.key];
      if (value != null && value != "") {
        if (
          this.field.templateOptions &&
          this.field.templateOptions.options &&
          this.field.templateOptions.options != null
        ) {
          this.field.templateOptions.options.forEach((item: any) => {
            if (item.value == value) {
              result = item.label;
            }
          });
        }
      }
    }
    return result;
  }

  get textMultiple() {
    let result = [];
    if (this.model.hasOwnProperty(this.field.key)) {
      let values = this.model[this.field.key];
      if (values != null && values instanceof Array) {
        if (
          this.field.templateOptions &&
          this.field.templateOptions.options &&
          this.field.templateOptions.options != null
        ) {
          values.forEach((element: any) => {
            this.to.options.forEach((item: any) => {
              if (item.value === element) {
                result.push(item.label);
              }
            });
          });
        }
      }
    }
    return result.join("");
  }

  get textLookUp() {
    let result = [];
    if (this.model.hasOwnProperty(this.key)) {
      if (this.model[this.key] != undefined && this.model[this.key] != null) {
        result = this.model[this.key].map((item: any) => {
          return item.label;
        });
      }
    }
    return result.join("");
  }

  get tips() {
    if (
      this.to != undefined &&
      this.to != null &&
      this.to.tips != undefined &&
      this.to.tips != null
    ) {
      return this.to.tips;
    }
    return "";
  }

  get attachment() {
    let result = [];
    if (this.model.hasOwnProperty(this.field.key)) {
      if (
        this.model[this.field.key] != null &&
        (this.model[this.field.key] as VXAttachmentFile)
      ) {
        result = this.model[this.field.key];
      }
    }
    return result;
  }

  get attachmentDisabled() {
    let result = false;
    if (this.to.hasOwnProperty("attachment")) {
      if (this.to.attachment.hasOwnProperty("limit")) {
        let limit = this.to.attachment.limit;
        if (limit != null) {
          let attachment: any = this.attachment;
          if (attachment != null) {
            result = this.attachment.length >= limit;
          }
        }
      }
    }
    return result;
  }

  get active(){
    let result = false;
    if(this.field["active"]!=undefined && this.field["active"]!=null && this.field["active"]==true){
      result = true;
    }
    return result;
  }

  get rownumber(){
    let result =0;
    if(this.field["active"]!=undefined && this.field["active"]!=null){
      result = this.field.rownumber;
    }
    return result;
  }
}

export abstract class Field extends FieldType {
  constructor() {
    super();
  }
}
