import {
  VXFormExtension,
  FieldValidatorFn,
  VXFormConfig
} from "../../services/vxform.config";
import { VXFormFieldConfigCache } from "../../components/vxform.field.config";
import { AbstractControl, Validators, ValidatorFn } from "@angular/forms";
import {
  isObject,
  VXFORM_VALIDATORS,
  defineHiddenProp,
  isUndefined,
  isPromise,
  wrapProperty
} from "../../utils";

export class FieldValidationExtension implements VXFormExtension {
  constructor(private vxFormConfig: VXFormConfig) {}

  onPopulate(field: VXFormFieldConfigCache) {
    if (!field.parent || !field.key) {
      return;
    }

    this.initFieldValidation(field, "validators");
    this.initFieldValidation(field, "asyncValidators");
  }

  private initFieldValidation(
    field: VXFormFieldConfigCache,
    type: "validators" | "asyncValidators"
  ) {
    if (!isUndefined(field["_" + type])) {
      if (field.formControl) {
        const validator =
          type === "validators"
            ? field.formControl.validator
            : field.formControl.asyncValidator;
        if (field["_" + type] !== validator) {
          field["_" + type] = validator;
        }
      }

      return;
    }

    const validators: ValidatorFn[] =
      type === "validators" ? [this.getPredefinedFieldValidation(field)] : [];
    if (field[type]) {
      for (const validatorName in field[type]) {
        if (
          validatorName === "validation" &&
          !Array.isArray(field[type].validation)
        ) {
          field[type].validation = [field[type].validation];
        }

        validatorName === "validation"
          ? validators.push(
              ...field[type].validation.map(v =>
                this.wrapNgValidatorFn(field, v)
              )
            )
          : validators.push(
              this.wrapNgValidatorFn(
                field,
                field[type][validatorName],
                validatorName
              )
            );
      }
    }

    defineHiddenProp(
      field,
      "_" + type,
      type === "validators"
        ? Validators.compose(validators)
        : Validators.composeAsync(validators as any)
    );
  }

  private getPredefinedFieldValidation(
    field: VXFormFieldConfigCache
  ): ValidatorFn {
    let VALIDATORS = [];
    VXFORM_VALIDATORS.forEach(opt =>
      wrapProperty(
        field.templateOptions,
        opt,
        ({ currentValue, firstChange }) => {
          VALIDATORS = VALIDATORS.filter(o => o !== opt);
          if (currentValue != null && currentValue !== false) {
            VALIDATORS.push(opt);
          }
          if (!firstChange && field.formControl) {
            field.formControl.updateValueAndValidity({ emitEvent: false });
          }
        }
      )
    );

    return (control: AbstractControl) => {
      if (VALIDATORS.length === 0) {
        return null;
      }

      return Validators.compose(
        VALIDATORS.map(opt => () => {
          const value = field.templateOptions[opt];
          switch (opt) {
            case "required":
              return Validators.required(control);
            case "pattern":
              return Validators.pattern(value)(control);
            case "minLength":
              return Validators.minLength(value)(control);
            case "maxLength":
              return Validators.maxLength(value)(control);
            case "min":
              return Validators.min(value)(control);
            case "max":
              return Validators.max(value)(control);
          }
        })
      )(control);
    };
  }

  private wrapNgValidatorFn(
    field: VXFormFieldConfigCache,
    validator: string | FieldValidatorFn,
    validatorName?: string
  ) {
    return (control: AbstractControl) => {
      let validatorFn = validator as FieldValidatorFn;
      if (typeof validator === "string") {
        validatorFn = this.vxFormConfig.getValidator(validator).validation;
      }
      if (isObject(validator)) {
        validatorFn = (validator as any).expression;
      }

      const isValid = validatorFn(control, field);
      if (validatorName) {
        if (isPromise(isValid)) {
          return isValid.then((result: boolean) => {
            if (field.options && field.options._markForCheck) {
              field.options._markForCheck(field);
            }

            return this.handleResult(field, result, {
              validatorName,
              validator
            });
          });
        }

        return this.handleResult(field, isValid, { validatorName, validator });
      }

      return isValid;
    };
  }

  private handleResult(
    field: VXFormFieldConfigCache,
    isValid,
    { validatorName, validator }
  ) {
    if (isObject(validator) && field.formControl && validator.errorPath) {
      const control = field.formControl.get(validator.errorPath);
      if (control) {
        const controlErrors = control.errors || {};
        if (!isValid) {
          control.setErrors({
            ...controlErrors,
            [validatorName]: { message: validator.message }
          });
        } else {
          delete controlErrors[validatorName];
          control.setErrors(
            Object.keys(controlErrors).length === 0 ? null : controlErrors
          );
        }
      }

      return isValid
        ? null
        : { [validatorName]: { errorPath: validator.errorPath } };
    }

    return isValid ? null : { [validatorName]: true };
  }
}
