import { ViewContainerRef, ViewChild } from "@angular/core";
import { FieldType } from "./field.type";
import { VXFormFieldConfig } from "../components/vxform.field.config";

export abstract class FieldWrapper<
  F extends VXFormFieldConfig = VXFormFieldConfig
> extends FieldType<F> {
  @ViewChild("fieldComponent", <any>{ read: ViewContainerRef, static: false })
  fieldComponent: ViewContainerRef;
}
