import {
  Injectable,
  InjectionToken,
  ComponentRef,
  ComponentFactoryResolver,
  Injector
} from "@angular/core";
import { ValidationErrors, AbstractControl } from "@angular/forms";
import { FieldType } from "./../templates/field.type";
import { reverseDeepMerge, defineHiddenProp } from "./../utils";
import {
  VXFormFieldConfig,
  VXFormFieldConfigCache
} from "../components/vxform.field.config";

export const VXFORM_CONFIG = new InjectionToken<VXFormConfig>("VXFORM_CONFIG");

export interface VXFormExtension {
  prePopulate?(field: VXFormFieldConfig): void;
  onPopulate?(field: VXFormFieldConfig): void;
  postPopulate?(field: VXFormFieldConfig): void;
}

@Injectable({ providedIn: "root" })
export class VXFormConfig {
  types: { [name: string]: TypeOption } = {};
  validators: { [name: string]: ValidatorOption } = {};
  wrappers: { [name: string]: WrapperOption } = {};
  messages: {
    [name: string]: string | ((error: any, field: VXFormFieldConfig) => string);
  } = {};
  templateManipulators: {
    preWrapper: ManipulatorWrapper[];
    postWrapper: ManipulatorWrapper[];
  } = {
    preWrapper: [],
    postWrapper: []
  };
  extras: ConfigOption["extras"] = {
    checkExpressionOn: "changeDetectionCheck",
    showError: function(field: FieldType) {
      return (
        field.formControl &&
        field.formControl.invalid &&
        (field.formControl.touched ||
          (field.options.parentForm && field.options.parentForm.submitted) ||
          (field.field.validation && field.field.validation.show))
      );
    }
  };
  extensions: { [name: string]: VXFormExtension } = {};

  appConfig: {
    url?: string;
    token?: string;
  };

  addConfig(config: ConfigOption) {
    if (config.types) {
      config.types.forEach(type => this.setType(type));
    }
    if (config.validators) {
      config.validators.forEach(validator => this.setValidator(validator));
    }
    if (config.wrappers) {
      config.wrappers.forEach(wrapper => this.setWrapper(wrapper));
    }
    if (config.manipulators) {
      config.manipulators.forEach(manipulator =>
        this.setManipulator(manipulator)
      );
    }
    if (config.validationMessages) {
      config.validationMessages.forEach(validation =>
        this.addValidatorMessage(validation.name, validation.message)
      );
    }
    if (config.extensions) {
      config.extensions.forEach(c => (this.extensions[c.name] = c.extension));
    }
    if (config.extras) {
      this.extras = { ...this.extras, ...config.extras };
    }
  }

  setType(options: TypeOption | TypeOption[]) {
    if (Array.isArray(options)) {
      options.forEach(option => this.setType(option));
    } else {
      if (!this.types[options.name]) {
        this.types[options.name] = <TypeOption>{ name: options.name };
      }

      ["component", "extends", "defaultOptions"].forEach(prop => {
        if (options.hasOwnProperty(prop)) {
          this.types[options.name][prop] = options[prop];
        }
      });

      if (options.wrappers) {
        options.wrappers.forEach(wrapper =>
          this.setTypeWrapper(options.name, wrapper)
        );
      }
    }
  }

  getType(name: string): TypeOption {
    if (!this.types[name]) {
    }

    this.mergeExtendedType(name);

    return this.types[name];
  }

  getMergedField(field: VXFormFieldConfig = {}): any {
    const type = this.getType(field.type);
    if (type.defaultOptions) {
      reverseDeepMerge(field, type.defaultOptions);
    }

    const extendDefaults =
      type.extends && this.getType(type.extends).defaultOptions;
    if (extendDefaults) {
      reverseDeepMerge(field, extendDefaults);
    }

    if (field && field.optionsTypes) {
      field.optionsTypes.forEach(option => {
        const defaultOptions = this.getType(option).defaultOptions;
        if (defaultOptions) {
          reverseDeepMerge(field, defaultOptions);
        }
      });
    }

    const componentRef = this.createComponent(field);
    if (
      componentRef &&
      componentRef.instance &&
      componentRef.instance.defaultOptions
    ) {
      reverseDeepMerge(field, componentRef.instance.defaultOptions);
    }

    if (!field.wrappers && type.wrappers) {
      field.wrappers = [...type.wrappers];
    }
  }

  createComponent(
    field: VXFormFieldConfigCache = {},
    resolver?: ComponentFactoryResolver,
    injector?: Injector
  ): ComponentRef<FieldType> {
    if (!field.type) {
      return;
    }

    const cf = field._componentFactory;
    if (
      cf &&
      field.type === cf.type &&
      cf.componentRef &&
      cf.componentRef.hostView &&
      !cf.componentRef.hostView.destroyed
    ) {
      return field._componentFactory.componentRef;
    }

    const type = this.getType(field.type);
    if (!resolver) {
      resolver = field.parent.options._componentFactoryResolver;
    }
    if (!injector) {
      injector = this.getFieldInjector(field);
    }

    defineHiddenProp(field, "_componentFactory", {
      type: field.type,
      component: type.component,
      componentRef: resolver
        ? resolver.resolveComponentFactory(type.component).create(injector)
        : null
    });

    return field._componentFactory.componentRef;
  }

  setWrapper(options: WrapperOption) {
    this.wrappers[options.name] = options;
    if (options.types) {
      options.types.forEach(type => {
        this.setTypeWrapper(type, options.name);
      });
    }
  }

  getWrapper(name: string): WrapperOption {
    if (!this.wrappers[name]) {
    }

    return this.wrappers[name];
  }

  setTypeWrapper(type: string, name: string) {
    if (!this.types[type]) {
      this.types[type] = <TypeOption>{};
    }
    if (!this.types[type].wrappers) {
      this.types[type].wrappers = [];
    }
    if (this.types[type].wrappers.indexOf(name) === -1) {
      this.types[type].wrappers.push(name);
    }
  }

  setValidator(options: ValidatorOption) {
    this.validators[options.name] = options;
  }

  getValidator(name: string): ValidatorOption {
    if (!this.validators[name]) {
    }

    return this.validators[name];
  }

  addValidatorMessage(
    name: string,
    message: string | ((error: any, field: VXFormFieldConfig) => string)
  ) {
    this.messages[name] = message;
  }

  getValidatorMessage(name: string) {
    return this.messages[name];
  }

  setManipulator(manipulator: ManipulatorOption) {
    new manipulator.class()[manipulator.method](this);
  }

  setAPPConfig(config: APPConfig) {
    this.appConfig = config;
  }

  private mergeExtendedType(name: string) {
    if (!this.types[name].extends) {
      return;
    }

    const extendedType = this.getType(this.types[name].extends);
    if (!this.types[name].component) {
      this.types[name].component = extendedType.component;
    }

    if (!this.types[name].wrappers) {
      this.types[name].wrappers = extendedType.wrappers;
    }
  }

  private getFieldInjector(field: VXFormFieldConfigCache = {}) {
    const parent = field.parent;
    if (parent._componentFactory && parent._componentFactory.componentRef) {
      return parent._componentFactory.componentRef.injector;
    }

    return parent.options._injector;
  }
}
export interface TypeOption {
  name: string;
  component?: any;
  wrappers?: string[];
  extends?: string;
  defaultOptions?: VXFormFieldConfig;
}

export interface WrapperOption {
  name: string;
  component: any;
  types?: string[];
}

export interface FieldValidatorFn {
  (c: AbstractControl, field: VXFormFieldConfig): ValidationErrors | null;
}

export interface ValidatorOption {
  name: string;
  validation: FieldValidatorFn;
}

export interface ExtensionOption {
  name: string;
  extension: VXFormExtension;
}

export interface ValidationMessageOption {
  name: string;
  message: string | ((error: any, field: VXFormFieldConfig) => string);
}

export interface ManipulatorOption {
  class?: { new (): any };
  method?: string;
}

export interface ManipulatorWrapper {
  (f: VXFormFieldConfig): string;
}

export interface TemplateManipulators {
  preWrapper?: ManipulatorWrapper[];
  postWrapper?: ManipulatorWrapper[];
}

export interface ConfigOption {
  types?: TypeOption[];
  wrappers?: WrapperOption[];
  validators?: ValidatorOption[];
  extensions?: ExtensionOption[];
  validationMessages?: ValidationMessageOption[];

  manipulators?: ManipulatorOption[];
  extras?: {
    fieldTransform?: any;
    immutable?: boolean;
    showError?: (field: FieldType) => boolean;
    checkExpressionOn?: "modelChange" | "changeDetectionCheck";
  };
}

export interface APPConfig {
  url?: string;
  token?: string;
}
