import { VXFormFieldConfig } from "./core";
import { isObservable } from "rxjs";
import { AbstractControl } from "@angular/forms";
import { VXFormFieldConfigCache } from "./components/vxform.field.config";

export function getFieldId(
  formId: string,
  field: VXFormFieldConfig,
  index: string | number
) {
  if (field.id) return field.id;
  let type = field.type;
  if (!type && field.template) type = "template";
  return [formId, type, field.key, index].join("_");
}

export function getKeyPath(field: VXFormFieldConfigCache): string[] {
  if (!field.key) {
    return [];
  }

  if (!field._keyPath || field._keyPath.key !== field.key) {
    const key =
      field.key.indexOf("[") === -1
        ? field.key
        : field.key.replace(/\[(\w+)\]/g, ".$1");

    field._keyPath = {
      key: field.key,
      path: key.indexOf(".") !== -1 ? key.split(".") : [key]
    };
  }

  return field._keyPath.path.slice(0);
}

export const VXFORM_VALIDATORS = [
  "required",
  "pattern",
  "minLength",
  "maxLength",
  "min",
  "max"
];

export function assignModelValue(model: any, paths: string[], value: any) {
  for (let i = 0; i < paths.length - 1; i++) {
    const path = paths[i];
    if (!model[path] || !isObject(model[path])) {
      model[path] = /^\d+$/.test(paths[i + 1]) ? [] : {};
    }

    model = model[path];
  }

  model[paths[paths.length - 1]] = clone(value);
}

export function getFieldValue(field: VXFormFieldConfig): any {
  let model = field.parent.model;
  for (const path of getKeyPath(field)) {
    if (!model) {
      return model;
    }
    model = model[path];
  }

  return model;
}

export function reverseDeepMerge(dest: any, ...args: any[]) {
  args.forEach(src => {
    for (let srcArg in src) {
      if (isNullOrUndefined(dest[srcArg]) || isBlankString(dest[srcArg])) {
        dest[srcArg] = clone(src[srcArg]);
      } else if (objAndSameType(dest[srcArg], src[srcArg])) {
        reverseDeepMerge(dest[srcArg], src[srcArg]);
      }
    }
  });
  return dest;
}

export function isNullOrUndefined(value: any) {
  return value === undefined || value === null;
}

export function isUndefined(value: any) {
  return value === undefined;
}

export function isBlankString(value: any) {
  return value === "";
}

export function isFunction(value: any) {
  return typeof value === "function";
}

export function objAndSameType(obj1: any, obj2: any) {
  return (
    isObject(obj1) &&
    isObject(obj2) &&
    Object.getPrototypeOf(obj1) === Object.getPrototypeOf(obj2) &&
    !(Array.isArray(obj1) || Array.isArray(obj2))
  );
}

export function isObject(x: any) {
  return x != null && typeof x === "object";
}

export function isPromise(obj: any): obj is Promise<any> {
  return !!obj && typeof obj.then === "function";
}

export function clone(value: any): any {
  if (
    !isObject(value) ||
    isObservable(value) ||
    value.changingThisBreaksApplicationSecurity ||
    ["RegExp", "FileList", "File", "Blob"].indexOf(value.constructor.name) !==
      -1
  ) {
    return value;
  }

  if (value._isAMomentObject && isFunction(value.clone)) {
    return value.clone();
  }

  if (value instanceof AbstractControl) {
    return null;
  }

  if (value instanceof Date) {
    return new Date(value.getTime());
  }

  if (Array.isArray(value)) {
    return value.slice(0).map(v => clone(v));
  }

  const proto = Object.getPrototypeOf(value);
  let c = Object.create(proto);
  c = Object.setPrototypeOf(c, proto);
  return Object.keys(value).reduce((newVal, prop) => {
    const propDesc = Object.getOwnPropertyDescriptor(value, prop);
    if (propDesc.get) {
      Object.defineProperty(newVal, prop, propDesc);
    } else {
      newVal[prop] = clone(value[prop]);
    }

    return newVal;
  }, c);
}

export function defineHiddenProp(field: any, prop: string, defaultValue: any) {
  Object.defineProperty(field, prop, {
    enumerable: false,
    writable: true,
    configurable: true
  });
  field[prop] = defaultValue;
}

export function wrapProperty<T = any>(
  field: any,
  prop: string,
  setFn: (change: {
    currentValue: T;
    previousValue?: T;
    firstChange: boolean;
  }) => void
) {
  defineHiddenProp(field, `___$${prop}`, field[prop]);
  setFn({ currentValue: field[prop], firstChange: true });

  Object.defineProperty(field, prop, {
    configurable: true,
    get: () => field[`___$${prop}`],
    set: currentValue => {
      if (currentValue !== field[`___$${prop}`]) {
        const previousValue = field[`___$${prop}`];
        field[`___$${prop}`] = currentValue;
        setFn({ previousValue, currentValue, firstChange: false });
      }
    }
  });
}
