import { Injectable, ComponentFactoryResolver, Injector } from "@angular/core";
import { FormGroup, FormArray } from "@angular/forms";
import { VXFormConfig } from "./vxform.config";
import {
  VXFormFieldConfig,
  VXFormOptions,
  VXFormFieldConfigCache,
  VXFormValueChangeEvent,
  VXFormOptionsCache
} from "../components/vxform.field.config";
import { Subject } from "rxjs";
import { defineHiddenProp } from "../utils"; 

@Injectable({ providedIn: "root" })
export class VXFormBuilder {
  constructor(  
    private vxFormConfig: VXFormConfig,
    private componentFactoryResolver: ComponentFactoryResolver,
    private injector: Injector
  ) {}

  buildForm(
    formControl: FormGroup | FormArray,
    fieldGroup: VXFormFieldConfig[] = [],
    model: any,
    options: VXFormOptions
  ) {
    if (!this.vxFormConfig.extensions.core) {
    }
 
    
    this._buildForm({
      fieldGroup,
      model,
      formControl,
      options: this._setOptions(options)
    }); 
  }

  private _buildForm(field: VXFormFieldConfigCache) {
    this.getExtensions().forEach(
      extension => extension.prePopulate && extension.prePopulate(field)
    );
    this.getExtensions().forEach(
      extension => extension.onPopulate && extension.onPopulate(field)
    );

    if (field.fieldGroup) {
      field.fieldGroup.forEach(f => this._buildForm(f));
    }

    this.getExtensions().forEach(
      extension => extension.postPopulate && extension.postPopulate(field)
    );
  }

  private getExtensions() {
    return Object.keys(this.vxFormConfig.extensions).map(
      name => this.vxFormConfig.extensions[name]
    );
  }

  private _setOptions(options: VXFormOptionsCache) {
    options = options || {};
    options.formState = options.formState || {};

    if (!options.showError) {
      options.showError = this.vxFormConfig.extras.showError;
    }

    if (!options.fieldChanges) {
      defineHiddenProp(
        options,
        "fieldChanges",
        new Subject<VXFormValueChangeEvent>()
      );
    }

    if (!options._componentFactoryResolver) {
      defineHiddenProp(
        options,
        "_componentFactoryResolver",
        this.componentFactoryResolver
      );
    }

    if (!options._injector) {
      defineHiddenProp(options, "_injector", this.injector);
    }

    if (!options._hiddenFieldsForCheck) {
      options._hiddenFieldsForCheck = [];
    }

    if (!options._markForCheck) {
      options._markForCheck = field => {
        if (field._componentRefs) {
          field._componentRefs.forEach(ref =>
            ref.changeDetectorRef.markForCheck()
          );
        }

        if (field.fieldGroup) {
          field.fieldGroup.forEach(f => options._markForCheck(f));
        }
      };
    }

    return options;
  }
}
