import {
  Component,
  DoCheck,
  OnChanges,
  Input,
  SimpleChanges,
  Optional,
  EventEmitter,
  Output,
  OnDestroy,
  Attribute,
  ViewChild,
  ElementRef
} from "@angular/core";
import { FormGroup, FormArray, FormGroupDirective } from "@angular/forms";
import {
  VXFormFieldConfig,
  VXFormOptions,
  VXFormOptionsCache
} from "./vxform.field.config";
import { VXFormBuilder } from "../services/vxform.builder";
import { VXFormConfig } from "../services/vxform.config";
import {
  assignModelValue,
  isNullOrUndefined,
  wrapProperty,
  clone,
  defineHiddenProp,
  getKeyPath
} from "../utils";
import { Subscription } from "rxjs";
import { debounceTime } from "rxjs/operators";
import { TestBaseUtils } from '../tools/test';

@Component({
  selector: "vxform",
  template: `
    <ng-container *ngIf="checkTest">
    <ng-container *ngFor="let field of fields">
      <vxform-field
        *ngIf="checkFieldShow(field)"
        hide-deprecation
        [form]="field.form"
        [options]="field.options"
        [model]="field.model"
        [field]="field"
      >
      </vxform-field>
    </ng-container>
    <ng-container #content>
      <ng-content></ng-content>
    </ng-container>
    </ng-container>
  `,
  host: {
    "[class]": "class"
  },
  providers: [VXFormBuilder,TestBaseUtils]
})
export class VXForm implements DoCheck, OnChanges, OnDestroy {
  @Input() form: FormGroup | FormArray;

  @Input()
  set model(model: any) {
    this._model = this.immutable ? clone(model) : model;
  }
  get model() {
    return this._model || {};
  }

  @Input()
  set fields(fields: VXFormFieldConfig[]) {
    this._fields = this.immutable ? clone(fields) : fields;
  }
  get fields() {
      return this._fields || [];
     
  }

  @Input()
  set options(options: VXFormOptions) {
    this._options = this.immutable ? clone(options) : options;
  }
  get options() {
    return this._options;
  }

  @Input() class: string;

  @Output() modelChange = new EventEmitter<any>();
  @ViewChild("content", { static: true }) set content(
    content: ElementRef<HTMLElement>
  ) {
    if (content && content.nativeElement.nextSibling) {
    }
  }

  @Input() status: number = 0;

  private immutable = false;
  private _model: any;
  private _fields: VXFormFieldConfig[];
  private _options: VXFormOptions;
  private initialModel: any;
  private modelChangeSubs: Subscription[] = [];
  private gogo:boolean = true;
  private enableCheckExprDebounce = false;
  private checkExpressionChange$ = this.modelChange
    .pipe(debounceTime(this.enableCheckExprDebounce ? 100 : 0))
    .subscribe(() => {
      this.enableCheckExprDebounce = true;
      this.checkExpressionChange();
      this.enableCheckExprDebounce = false;
    });

  constructor(
    private vxFormBuilder: VXFormBuilder,
    private vxFormConfig: VXFormConfig,
    private test:TestBaseUtils,
    @Attribute("immutable") immutable,
    @Optional() private parentFormGroup: FormGroupDirective
  ) {
    if (immutable !== null) {
    }

    this.immutable = immutable !== null || !!vxFormConfig.extras.immutable;
    // let linces = localStorage.getItem("VX_FORM_LINCES")
    // if(linces!=undefined && linces!=null && linces!=""){
    //   this.gogo = this.test.t1(linces);
    // } 
  }

  ngDoCheck() {
    if (this.vxFormConfig.extras.checkExpressionOn === "changeDetectionCheck") {
      this.checkExpressionChange();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.fields || changes.form || changes.model) {
      this.form = this.form || new FormGroup({});
      this.setOptions();
      this.clearModelSubscriptions();
      this.vxFormBuilder.buildForm(
        this.form,
        this.fields,
        this.model,
        this.options
      );
      this.trackModelChanges(this.fields);
      this.options.updateInitialValue();
    }
  }

  ngOnDestroy() {
    this.clearModelSubscriptions();
    this.checkExpressionChange$.unsubscribe();
  }

  changeModel(event: { key: string; value: any }) {
    assignModelValue(this.model, event.key.split("."), event.value);
    this.modelChange.emit(clone(this.model));
  }

  setOptions() { 
    if (!this.options) {
      this.options = {};
    }

    if (!this.options.resetModel) {
      this.options.resetModel = (model?: any) => {
        model = clone(isNullOrUndefined(model) ? this.initialModel : model);
        if (this.model) {
          Object.keys(this.model).forEach(k => delete this.model[k]);
          Object.assign(this.model, model || {});
        }

        (<VXFormOptionsCache>this.options)._buildForm();

        if (
          this.options.parentForm &&
          this.options.parentForm.control === this.form
        ) {
          this.options.parentForm.resetForm(model);
        } else {
          this.form.reset(model);
        }
      };
    }

    if (!this.options.parentForm && this.parentFormGroup) {
      defineHiddenProp(this.options, "parentForm", this.parentFormGroup);
      wrapProperty(this.options.parentForm, "submitted", ({ firstChange }) => {
        if (!firstChange) {
          (<VXFormOptionsCache>this.options)._markForCheck({
            fieldGroup: this.fields,
            model: this.model,
            formControl: this.form,
            options: this.options
          });
        }
      });
    }

    if (!this.options.updateInitialValue) {
      this.options.updateInitialValue = () =>
        (this.initialModel = clone(this.model));
    }

    if (!(<VXFormOptionsCache>this.options)._buildForm) {
      (<VXFormOptionsCache>this.options)._buildForm = (
        emitModelChange = false
      ) => {
        this.clearModelSubscriptions();
        this.vxFormBuilder.buildForm(
          this.form,
          this.fields,
          this.model,
          this.options
        );
        this.trackModelChanges(this.fields);

        if (emitModelChange) {
          this.modelChange.emit(clone(this.model));
        }
      };
    }
  }

  private checkExpressionChange() {
    if (this.options && (<VXFormOptionsCache>this.options)._checkField) {
      (<VXFormOptionsCache>this.options)._checkField({
        fieldGroup: this.fields,
        model: this.model,
        formControl: this.form,
        options: this.options
      });
    }
  }

  private trackModelChanges(
    fields: VXFormFieldConfig[],
    rootKey: string[] = []
  ) {
    fields.forEach(field => {
      field.status = this.status;
      if (field.key && !field.fieldGroup) {
        const control = field.formControl;
        let valueChanges = control.valueChanges;

        const { updateOn, debounce } = field.modelOptions;
        if (
          (!updateOn || updateOn === "change") &&
          debounce &&
          debounce.default > 0
        ) {
          valueChanges = control.valueChanges.pipe(
            debounceTime(debounce.default)
          );
        }

        this.modelChangeSubs.push(
          valueChanges.subscribe(value => {
            if ((control as any)._onChange.length > 1) {
              control.patchValue(value, { emitEvent: false });
            }

            if (field.parsers && field.parsers.length > 0) {
              field.parsers.forEach(parserFn => (value = parserFn(value)));
            }

            this.changeModel({
              key: [...rootKey, ...getKeyPath(field)].join("."),
              value
            });
          })
        );
      }

      if (field.fieldGroup && field.fieldGroup.length > 0) {
        this.trackModelChanges(
          field.fieldGroup,
          field.key ? [...rootKey, ...getKeyPath(field)] : rootKey
        );
      }
    });
  }

  private clearModelSubscriptions() {
    this.modelChangeSubs.forEach(sub => sub.unsubscribe());
    this.modelChangeSubs = [];
  }

  checkFieldShow(field: VXFormFieldConfig) {
    let result = true;
    if (field && field.hasOwnProperty("show")) {
      result = field.show;
    }
    return result;
  }

  get checkTest(){ 
    return this.gogo;
  }
  set checkTest(value:boolean){
    this.gogo = value;
  }
}
