import {
  Component,
  EventEmitter,
  Input,
  Output,
  ViewContainerRef,
  ViewChild,
  ComponentRef,
  SimpleChanges,
  Attribute,
  ComponentFactoryResolver,
  OnInit,
  OnChanges,
  OnDestroy,
  DoCheck,
  AfterContentInit,
  AfterContentChecked,
  AfterViewInit,
  AfterViewChecked,
  Injector
} from "@angular/core";
import { FormGroup } from "@angular/forms";
import { VXFormConfig } from "../services/vxform.config";
import {
  VXFormFieldConfig,
  VXFormOptions,
  VXFormFieldConfigCache
} from "./vxform.field.config";
import { defineHiddenProp, wrapProperty } from "../utils";
import { FieldWrapper } from "../templates/field.wrapper";
import { FieldType } from "../templates/field.type";

@Component({
  selector: "vxform-field",
  template: `
    <ng-template #container></ng-template>
  `,
  host: {
    "[style.display]": 'field.hide ? "none":""',
    //"[class]": "field.className? field.className : className"
    "[class]":
      "field.type=='text'?'panel-'+field.type+' panel-'+field.typeold: 'panel-'+field.type"
  }
})
export class VXFormField
  implements
    OnInit,
    OnChanges,
    DoCheck,
    AfterContentInit,
    AfterContentChecked,
    AfterViewInit,
    AfterViewChecked,
    OnDestroy {
  @Input() field: VXFormFieldConfig;
  @Input("class") className: string = "";

  warnDeprecation = false;

  @Input() set model(m: any) {
    this.warnDeprecation && console.warn(`:${this.constructor.name}`);
  }

  @Input() set form(form: FormGroup) {
    this.warnDeprecation && console.warn(`:${this.constructor.name}`);
  }

  @Input() set options(options: VXFormOptions) {
    this.warnDeprecation && console.warn(`:${this.constructor.name}`);
  }

  @Input() set rownumber(value:number){
    if(value!=undefined && value!=null && value>-1){
      this.field.rownumber = value;
    }
  }

  @Output() modelChange: EventEmitter<any> = new EventEmitter();

  @ViewChild("container", <any>{ read: ViewContainerRef, static: true })
  containerRef: ViewContainerRef;

  constructor(
    private vxFormConfig: VXFormConfig,
    private componentFactoryResolver: ComponentFactoryResolver,
    private injector: Injector,
    @Attribute("hide-deprecation") hideDeprecation
  ) {
    this.warnDeprecation = hideDeprecation === null;
  }

  ngAfterContentInit() {
    this.triggerHook("afterContentInit");
  }

  ngAfterContentChecked() {
    this.triggerHook("afterContentChecked");
  }

  ngAfterViewInit() {
    this.triggerHook("afterViewInit");
  }

  ngAfterViewChecked() {
    this.triggerHook("afterViewChecked");
  }

  ngDoCheck() {
    this.triggerHook("doCheck");
  }

  ngOnInit() {
    this.triggerHook("onInit");
  }

  ngOnChanges(changes: SimpleChanges) {
    this.triggerHook("onChanges", changes);
  }

  ngOnDestroy() {
    this.field && defineHiddenProp(this.field, "_componentRefs", []);
    this.triggerHook("onDestroy");
  }

  private renderField(
    containerRef: ViewContainerRef,
    f: VXFormFieldConfigCache,
    wrappers: string[]
  ) {
    if (this.containerRef === containerRef) {
      defineHiddenProp(this.field, "_componentRefs", []);
      this.containerRef.clear();
    }

    if (wrappers && wrappers.length > 0) {
      const [wrapper, ...wps] = wrappers;
      const { component } = this.vxFormConfig.getWrapper(wrapper);
      const cfr =
        f.options && f.options._componentFactoryResolver
          ? f.options._componentFactoryResolver
          : this.componentFactoryResolver;

      const ref = containerRef.createComponent<FieldWrapper>(
        cfr.resolveComponentFactory(component)
      );
      this.attachComponentRef(ref, f);
      wrapProperty(
        ref.instance,
        "fieldComponent",
        ({ currentValue, firstChange }) => {
          if (currentValue) {
            this.renderField(currentValue as ViewContainerRef, f, wps);
            !firstChange && ref.changeDetectorRef.detectChanges();
          }
        }
      );
    } else {
      const ref = this.vxFormConfig.createComponent(
        f,
        this.componentFactoryResolver,
        this.injector
      );
      if (ref) {
        this.attachComponentRef(ref, f);
        containerRef.insert(ref.hostView);
      }
    }
  }

  private triggerHook(name: string, changes?: SimpleChanges) {
    if (this.field.hooks && this.field.hooks[name]) {
      if (!changes || changes.field) {
        this.field.hooks[name](this.field);
      }
    }

    if (this.field.lifecycle && this.field.lifecycle[name]) {
      this.field.lifecycle[name](
        this.field.form,
        this.field,
        this.field.model,
        this.field.options
      );
    }

    if (name === "onChanges" && changes.field) {
      this.renderField(this.containerRef, this.field, this.field.wrappers);
    }
  }

  private attachComponentRef<T extends FieldType>(
    ref: ComponentRef<T>,
    field: VXFormFieldConfigCache
  ) {
    field._componentRefs.push(ref);
    Object.assign(ref.instance, { field });
  }
}
